.collapse:not(.show) {
  display: none;
}

:root {
  --navigation-padding-x: 1rem;
  --navigation-padding-y: .5rem;
  --navigation-transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out;
  --navigation-disabled-color: #718096;
  --navigation-tabs-border-color: #e2e8f0;
  --navigation-tabs-border-radius: .25rem;
  --navigation-tabs-border-width: 1px;
  --navigation-tabs-active-color: #4a5568;
  --navigation-tabs-active-background-color: #fff;
  --navigation-tabs-active-border-color: #e2e8f0;
  --navigation-tabs-hover-border-color: #e2e8f0;
  --navigation-pills-border-radius: .25rem;
  --navigation-pills-padding-y: .5rem;
  --navigation-pills-active-color: #fff;
  --navigation-pills-active-background-color: #4299e1;
}

.nav {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.nav-link {
  display: block;
  padding: .5rem 1rem;
  text-decoration: none;
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out;
}

.nav-link:hover, .nav-link:focus {
  text-decoration: none;
}

.nav-link.disabled {
  color: #718096;
  pointer-events: none;
  cursor: default;
}

.nav-tabs {
  border-bottom: 1px solid #e2e8f0;
}

.nav-tabs .nav-link {
  margin-bottom: -1px;
  border: 1px solid transparent;
  border-top-left-radius: .25rem;
  border-top-right-radius: .25rem;
}

.nav-tabs .nav-link:hover, .nav-tabs .nav-link:focus {
  border-color: #e2e8f0 #e2e8f0 #e2e8f0;
}

.nav-tabs .nav-link.disabled {
  color: #718096;
  background-color: transparent;
  border-color: transparent;
}

.nav-tabs .nav-link.active, .nav-tabs .nav-item.show .nav-link {
  color: #4a5568;
  background-color: #fff;
  border-color: #e2e8f0 #e2e8f0 #fff;
}

.nav-tabs .dropdown-menu {
  margin-top: -1px !important;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.nav-pills .nav-link {
  border-radius: .25rem;
}

.nav-pills .nav-link.active, .nav-pills .show > .nav-link {
  color: #fff;
  background-color: #4299e1;
}

.nav-fill .nav-item {
  flex: 1 1 auto;
  text-align: center;
}

.nav-justified .nav-item {
  flex-basis: 0;
  flex-grow: 1;
  text-align: center;
}

.tab-content > .tab-pane {
  display: none;
}

.tab-content > .active {
  display: block;
}

:root {
  --modal-inner-padding: 1rem;
  --modal-dialog-margin: .5rem;
  --modal-dialog-up-margin-y: 1.75rem;
  --modal-title-line-height: 1.5px;
  --modal-content-background-color: #fff;
  --modal-content-border-color: rgba(0, 0, 0, 0.2);
  --modal-content-border-width: 1px;
  --modal-content-border-radius: .3rem;
  --modal-content-xs-box-shadow: 0 .5rem 1rem rgba(0, 0, 0, 0.075);
  --modal-content-inner-border-radius: calc(.3rem - 1px);
  --modal-content-up-box-shadow: 0 .5rem 1rem rgba(0, 0, 0, 0.15);
  --modal-backdrop-background-color: #000;
  --modal-backdrop-opacity: 0.5px;
  --modal-backdrop-z-index: 1040px;
  --modal-header-border-color: #e2e8f0;
  --modal-header-border-width: 1px;
  --modal-header-padding-y: 1rem;
  --modal-header-padding-x: 1rem;
  --modal-header-padding: 1rem 1rem;
  --modal-footer-border-color: #e2e8f0;
  --modal-footer-border-width: 1px;
  --modal-footer-margin-between: .5rem;
  --modal-transition: transform .3s ease-out;
  --modal-transform-fade: translate(0, -50px);
  --modal-transform-scale: scale(1.02);
  --modal-transform-show: none;
  --modal-sm: 300px;
  --modal-md: 500px;
  --modal-lg: 800px;
  --modal-xl: 1140px;
  --modal-z-index: 1050px;
}

.modal-open {
  overflow: hidden;
}

.modal-open .modal {
  overflow-x: hidden;
  overflow-y: auto;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  display: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0;
}

.modal-dialog {
  position: relative;
  width: auto;
  margin: .5rem;
  pointer-events: none;
}

.modal.fade .modal-dialog {
  transition: transform .3s ease-out;
  transform: translate(0, -50px);
}

.modal.show .modal-dialog {
  transform: none;
}

.modal.modal-static .modal-dialog {
  transform: scale(1.02);
}

.modal-dialog-scrollable {
  max-height: calc(100% - NaNNaN);
}

.modal-content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: .3rem;
  box-shadow: 0 .5rem 1rem rgba(0, 0, 0, 0.075);
  outline: 0;
}

.modal-body {
  position: relative;
  flex: 1 1 auto;
  padding: 1rem;
}

.modal-dialog-centered {
  display: flex;
  align-items: center;
  min-height: calc(100% - NaNNaN);
}

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: #000;
}

.modal-backdrop.fade {
  opacity: 0;
}

.modal-backdrop.show {
  opacity: 0.5;
}

.modal-header {
  display: flex;
  flex-shrink: 0;
  align-items: flex-start;
  justify-content: space-between;
  padding: 1rem 1rem;
  border-bottom: 1px solid #e2e8f0;
  border-top-left-radius: calc(.3rem - 1px);
  border-top-right-radius: calc(.3rem - 1px);
}

.modal-header .close {
  padding: 1rem 1rem;
  margin: (-1rem) (-1rem) (-1rem) auto;
}

.modal-title {
  margin-bottom: 0;
  line-height: 1.5;
}

.modal-footer {
  display: flex;
  flex-wrap: wrap;
  flex-shrink: 0;
  align-items: center;
  justify-content: flex-end;
  padding: calc(1rem - NaNNaN;
  border-top: 1px solid #e2e8f0;
  border-bottom-left-radius: calc(.3rem - 1px);
}

.modal-footer > * {
  margin: NaNNaN;
}

.modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 50px;
  height: 50px;
  overflow: scroll;
}